@import url(https://fonts.googleapis.com/css?family=Libre+Franklin&display=swap);
html, body, #root {
  min-height: 100vh;
  background-color: #0C0C0C; }

*::-moz-selection {
  background: #F1284C; }

*::selection {
  background: #F1284C; }

body {
  margin: 0;
  overflow-x: hidden; }

html {
  font-size: calc(100% * 0.625); }

body {
  font-size: 1.6rem;
  line-height: 1.8;
  color: #eee;
  font-family: "Libre Franklin", sans-serif; }

button, input {
  font-family: "Libre Franklin", sans-serif;
  font-size: 1.6rem; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1;
  color: #fff;
  font-family: "Gilroy"; }

.app {
  width: 100%;
  min-height: 100vh; }

.button {
  padding: 1.5rem 3rem;
  border-radius: 3rem;
  background-color: #E8294C;
  border-color: #E8294C;
  font-weight: bold;
  color: #eee;
  border-width: 0;
  box-sizing: border-box;
  min-width: 17.5rem;
  cursor: pointer;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s; }
  .button:focus, .button:active {
    outline: none; }
  .button:hover {
    opacity: 0.9; }
  .button--secondary {
    background-color: #8B0171;
    border-color: #8B0171; }
  .button--outline {
    border-width: 0.2rem;
    border-style: solid;
    background-color: transparent; }

.section {
  width: 100%;
  min-height: 40rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 20rem;
  position: relative;
  padding-top: 5rem; }
  .section--glown {
    background-image: -webkit-radial-gradient(50% -100%, circle, rgba(72, 162, 245, 0.7), rgba(38, 91, 139, 0) 70%);
    background-image: radial-gradient(circle at 50% -100%, rgba(72, 162, 245, 0.7), rgba(38, 91, 139, 0) 70%); }
  .section--without-line {
    padding-bottom: 8rem; }
  .section__layout {
    width: 95rem; }
  .section__line {
    bottom: 0;
    position: absolute;
    z-index: 0; }

.line {
  width: 100%;
  pointer-events: none; }

.heading-section {
  position: relative;
  display: flex;
  flex-direction: column; }
  .heading-section__logo {
    align-self: flex-start; }
  .heading-section__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 60rem;
    margin-top: 5rem; }
  .heading-section__action {
    margin-top: 1rem; }

.section-info {
  position: relative; }
  .section-info--rtl {
    text-align: right;
    align-items: flex-end; }
    .section-info--rtl .section-info {
      align-items: flex-end; }
      .section-info--rtl .section-info__glow {
        left: 100%;
        -webkit-transform: translate(-55%, -35%);
                transform: translate(-55%, -35%); }
  .section-info__glow {
    position: absolute;
    -webkit-transform: translate(-30%, -30%);
            transform: translate(-30%, -30%);
    pointer-events: none; }
  .section-info__title {
    font-size: 7.6rem;
    display: flex;
    flex-direction: column;
    margin: 1rem 0; }
  .section-info__desc {
    display: flex;
    flex-direction: column;
    font-style: italic; }

.shooting-star {
  position: absolute;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0); }
  .shooting-star--vertical {
    -webkit-transform: translate(-50%, 0) rotate(-90deg);
            transform: translate(-50%, 0) rotate(-90deg); }

.dot {
  position: absolute;
  opacity: 0;
  z-index: 0;
  width: 0.6rem;
  height: 0.6rem;
  background-color: #E8294C; }
  .dot--white {
    background-color: #eee; }
  .dot--secondary {
    background-color: #8B0171; }
  .dot--tertiary {
    background-color: #45AAF1; }

.control-section {
  position: relative;
  padding-top: 0rem;
  display: flex;
  flex-direction: row-reverse; }
  .control-section__content {
    max-width: 60rem;
    align-items: flex-end;
    margin-top: 5rem; }
  .control-section__action {
    margin-top: 1rem; }

.fluid-section {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }
  .fluid-section__actions {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    flex-basis: 30rem; }

.join-us {
  margin-top: 4rem; }
  .join-us__input {
    height: 4.5rem;
    border: none;
    border-radius: 3rem;
    max-width: 30rem;
    width: 100%;
    padding: 0 4rem 0 2rem; }
    .join-us__input:active, .join-us__input:focus {
      outline: none; }
  .join-us__button {
    margin-left: -4rem; }

.get-ahead {
  position: relative; }

.easy-animation-section {
  position: relative;
  padding-top: 0rem;
  display: flex;
  flex-direction: row-reverse; }
  .easy-animation-section__content {
    max-width: 60rem;
    align-items: flex-end;
    margin-top: 5rem; }
  .easy-animation-section__action {
    margin-top: 1rem; }

.footer {
  display: flex;
  justify-content: center; }
  .footer__layout {
    border-top: 0.3rem solid #eee;
    padding-top: 5rem;
    max-width: 95rem;
    width: 100%;
    display: flex; }

.footer-column {
  flex: 1 0; }
  .footer-column__description {
    font-style: italic; }

